/* Header */

header {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 10% 15px;
  background-color: rgba(58, 58, 59, 0.8);
  margin-top: 5px;
  z-index: 1;
}


header nav {
  display: flex;
  align-items: center;
  margin-right: -15px;
  font-size: 1em;
}

header nav li {
  margin: 0 15px;
}

header a,
header nav li a {
  color: rgba(255, 255, 255, 0.5);
}

header h2 {
  font-weight: 700;
}

header a.home:hover {
  color: black;
}

.grow { transition: all .1s ease-in-out; }

.grow:hover {
  color: black;
}

@media(max-width: 615px) {
  header {
    padding: 20px 50px;
  }
}
@media(max-width: 560px) {
  header {
    padding: 20px 30px;
  }
}
@media(max-width: 517px) {
  header {
    padding: 10px 15px;
  }
}
@media(max-width: 473px) {
  header {
    padding: 5px 10px;
  }
  header nav {
    margin-right: 0;
    margin-left: 15px;
    vertical-align: text-bottom;
  }
  header nav li {
    margin: 4px 10px 0 0;
  }
}

/* Footer Section */

footer {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 20px 20px;
  background-color: rgba(58, 58, 59, 0.7);
  bottom: 0;
  /* margin-top: 10px; */
}

@media (max-width:1075px) {
  footer {
    padding: 20px;
  }
}

@media (max-width:600px) {
  footer {
    padding: 5px;
  }
}

footer ul li {
  display: inline-flex;
  margin-right: 3em;
}
footer ul li:nth-of-type(3) {
  margin-right: 0em;
}

footer a:link {
  color: inherit;
  margin-right: 1px;
}
footer a:visited {
  color: inherit;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* To style font */
html {
  font: normal 16px 'Nunito', sans-serif;
  color: rgba(255, 255, 255, 0.7);
  background-color: #3A3A3B;
}

@media (min-width: 576px) {
	html {
		font-size: 14px;
	}
}

@media (min-width: 768px) {
	html {
		font-size: 16px;
	}
}

@media (min-width: 992px) {
	html {
		font-size: 18px;
	}
}

@media (min-width: 1200px) {
	html {
		font-size: 20px;
	}
}

ul,
nav {
  list-style: none;
}

ul {
  overflow: auto;
}

a {
  text-decoration: none;
}

section {
  background-color: rgba(255, 255, 255, 0.0);
  margin: 0 100px;
}

hr {
  height: 100px;
  width: 5px;
  color: #3A3A3B;
}

/* Hero Section */

section.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  min-height: 100vh;

}

section.hero h1 {
  font-size: 3.5em;
}

section.hero h3 {
  font-size: 2.0em;
  font-weight: 400;
}


/* About Section */

section.about {
  display: flex;
  height: 100vh;
}

@media(max-width: 600px){
  section.about {
    display: flex;
    flex-direction: column;
  }
}

div.about-text {
  border-right: solid #3A3A3B;
  flex-basis: 40%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  padding-top: 30px;
}

section div ul.about-text li {
  margin: 15px 30px;
}

div.about-img {
  flex-basis: 60%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 75px;
  max-height: 350px;
  align-content: center;
  display: flex;
  justify-content: space-around;
  /* background-size: cover;
  background-clip: inherit; */
}

div img.self {
  position: relative;
  height: 350px;
}

@media (max-width: 950px) {
  div.about-img,
  div.about-text {
    flex-basis: 50%;
  }

  section div ul.about-text li {
    margin: 15px 15px;
  }

}



section.hero {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 200px 7vw; }
  section.hero h1 {
    margin-bottom: 1.8em; }
  section.hero a {
    text-decoration: none;
    color: inherit;
    opacity: 0.8; }
  section.hero a:hover {
    opacity: 1; }
  section.hero a:active {
    font-size: 1.05em; }
  section.hero p {
    text-align: center;
    line-height: 2;
    font-size: 1.5em; }

@media (max-width: 720px) {
  section.hero {
    margin: auto;
    width: 100%; } }

html {
  font-size: 16px; }

section.hero {
  margin: 0; }
  section.hero h1 {
    margin-bottom: 158px; }

.portfolio-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center; }

@media (max-width: 840px) {
  .portfolio-container {
    flex-direction: column; } }

.portfolio-square {
  display: flex;
  flex-direction: column;
  position: relative;
  border: 3px solid rgba(255, 255, 255, 0.5);
  border-radius: 10px;
  padding: 10px;
  width: 350px;
  height: 500px;
  font-size: 0.8em;
  margin-bottom: 7vw; }
  .portfolio-square .portfolio-thumbnail {
    flex-basis: 40%;
    background-position: center;
    background-size: cover;
    opacity: 0.9; }
  .portfolio-square .portfolio-content {
    flex-basis: 55%;
    padding: 20px 30px 20px; }
    .portfolio-square .portfolio-content h3 {
      font-size: 1.6em;
      margin-bottom: 5px;
      line-height: 1.2; }
    .portfolio-square .portfolio-content h5 {
      margin-bottom: 20px; }
    .portfolio-square .portfolio-content p {
      line-height: 1.6;
      text-align: left;
      font-size: 16px; }
  .portfolio-square .portfolio-details {
    flex-basis: 5%;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    padding-top: 2px;
    font-size: 14px;
    display: flex;
    justify-content: space-around;
    align-items: center; }

